import React from "react";
import { ListGroup } from "../list-group/ListGroup";

export const StoryCard = ({ title, description, characters, comics }) => {
  return (
    <div className="card">
      <div className="card-header">Stories</div>
      <div className="card-body">
        <h2 className="card-title">{title}</h2>
        <p className="card-text">{description}</p>
        <hr />
        <div className="row">
          {characters.length > 0 && (
            <div className="col-md-6 col-sm-12">
              <h3>Characters</h3>
              <ul className="list-group">
                {characters.map((character, index) => (
                  <ListGroup key={index} message={character.name} />
                ))}
              </ul>
            </div>
          )}
          <div className="col-md-6 col-sm-12">
            <h3>Comics</h3>
            <ul className="list-group">
              {comics.map((comic, index) => (
                <ListGroup key={index} message={comic.name} />
              ))}
            </ul>
          </div>
        </div>
      </div>
    </div>
  );
};
