import { Paginator } from "../../components/paginator/Paginator";
import React, { useCallback, useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { ComicCard } from "../../components/comic-card/ComicCard";
import { Loading } from "../../components/loading/Loading";
import { InputFilter } from "../../components/input-filter/InputFilter";
import { getComics } from "../../store/actions/comics/comicAction";
import {
  resetPagination,
  setPagination,
} from "../../store/actions/pagination/paginationAction";
import { genericFilteredItem } from "../../util/filterUtil";

export const Comic = () => {
  const comics = useSelector((state) => state.comics.data);
  const isLoading = useSelector((state) => state.comics.isLoading);
  const paginator = useSelector((state) => state.paginator.paginator);
  const dispatch = useDispatch();
  const [currentPage, setCurrentPage] = useState(1);

  useEffect(() => {
    dispatch(getComics());
  }, [dispatch]);

  useEffect(() => {
    if (comics.length) {
      dispatch(setPagination(1, comics));
      return () => dispatch(resetPagination());
    }
  }, [comics, setCurrentPage, dispatch]);

  const onPaginator = useCallback(
    (currentPage) => {
      dispatch(setPagination(currentPage, comics));
      setCurrentPage(currentPage);
    },
    [comics, setCurrentPage, dispatch]
  );

  const onFilter = useCallback(
    (filterBy, filterText) => {
      if (filterText.length) {
        const paginate = genericFilteredItem(filterText, filterBy, 9, comics);
        dispatch(setPagination(1, paginate.filteredData));
      } else {
        onPaginator(currentPage);
      }
    },
    [comics, currentPage, dispatch, onPaginator]
  );

  return (
    <article className="container">
      <h2 className="mt-3">COMICS</h2>
      <InputFilter
        onFilter={onFilter}
        filterByOptions={["title", "format", "issueNumber"]}
      />
      <section className="row">
        {isLoading || comics.length === 0 ? (
          <Loading />
        ) : (
          paginator.paginatedData.map((comic, index) => {
            return (
              <article key={comic.id} className="col-md-6 col-sm-12 mt-3">
                <ComicCard
                  title={comic.title}
                  image={`${comic.thumbnail ? comic.thumbnail.path : ""}.${
                    comic.thumbnail ? comic.thumbnail.extension : ""
                  }`}
                  characters={comic.characters ? comic.characters.items : []}
                  stories={comic.stories ? comic.stories.items : []}
                />
              </article>
            );
          })
        )}
      </section>
      <section className="row">
        <article className="col-12">
          <Paginator
            onPaginate={onPaginator}
            paginatorData={paginator.paginatorData}
            pages={paginator.pages}
          />
        </article>
      </section>
    </article>
  );
};
