import React, { useEffect } from "react";
import { Slider } from "./../../components/slider/Slider";
import { CharacterCard } from "../../components/character-card/CharacterCard";
import { ComicCard } from "../../components/comic-card/ComicCard";
import { Loading } from "../../components/loading/Loading";
import { Alert } from "../../components/alert/Alert";
import { useDispatch, useSelector } from "react-redux";
import { getCharacters } from "../../store/actions/characters/characterAction";
import { getComics } from "../../store/actions/comics/comicAction";

import "./home.scss";

export const Home = () => {
  const characters = useSelector((state) => state.characters.data);
  const comics = useSelector((state) => state.comics.data);
  const isLoadingCharacters = useSelector(
    (state) => state.characters.isLoading
  );
  console.log(isLoadingCharacters);
  const charactersError = useSelector((state) => state.characters.error);
  const comicsError = useSelector((state) => state.comics.error);
  const isLoadingComics = useSelector((state) => state.comics.isLoading);
  const charactersWithDescription = characters.filter(
    (character) => character.description !== ""
  );
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(getCharacters());
    dispatch(getComics());
  }, [dispatch]);

  return (
    <article className="container mt-3 rounded">
      <section className="row text-center">
        <article className="col-12">
          <h2 className="text-center">CHARACTERS</h2>
        </article>
        <article className="card-deck w-100">
          {isLoadingCharacters || characters.length === 0 ? (
            <div className="col-12 text-center">
              <Loading />
            </div>
          ) : (
            charactersWithDescription.slice(0, 4).map((character) => (
              <div key={character.id} className="col-md-6 col-sm-12 mt-3">
                <CharacterCard
                  characterId={character.id}
                  title={character.name}
                  description={character.description}
                  image={`${character.thumbnail.path}.${character.thumbnail.extension}`}
                />
              </div>
            ))
          )}
        </article>
      </section>

      <div className="slider-container mt-3">
        {isLoadingCharacters || characters.length === 0 ? (
          <section className="mb-5 d-flex justify-content-center">
            <Loading />
          </section>
        ) : (
          <Slider
            sliderId="characterSlider"
            indicators={characters.slice(0, 5)}
          >
            {characters.slice(0, 5).map((character, index) => (
              <Slider.Content
                key={character.id}
                title={character.name}
                message="Marvel Characters"
                active={index === 0 ? true : false}
                image={`/assets/images/${index + 1}.jpg`}
              ></Slider.Content>
            ))}
          </Slider>
        )}
        {charactersError && (
          <Alert type="alert-danger" message={charactersError} />
        )}
      </div>

      <section className="mt-5">
        <h2 className="text-center">COMICS</h2>
        <article className="row mt-4">
          {isLoadingComics || comics.length === 0 ? (
            <section className="mb-5 d-flex text-center w-100 justify-content-center">
              <Loading />
            </section>
          ) : (
            comics.slice(0, 5).map((comic) => (
              <div key={comic.id} className="col-md-6 col-sm-12">
                <ComicCard
                  title={comic.title}
                  image={`${comic.thumbnail.path}.${comic.thumbnail.extension}`}
                  characters={comic.characters.items}
                  stories={comic.stories.items}
                />
              </div>
            ))
          )}
        </article>
        {comicsError && <Alert type="alert-danger" message={comicsError} />}
      </section>
    </article>
  );
};
