import React from "react";
import { ListGroup } from "../list-group/ListGroup";
export const ComicCard = ({ title, image, characters, stories }) => {
  return (
    <div className="card mb-3" style={{ maxWidth: "540px" }}>
      <div className="row no-gutters">
        <div className="col-md-4">
          <img src={image} className="card-img" alt="Comic" />
        </div>
        <div className="col-md-8">
          <div className="card-body">
            <h2 className="card-title">{title}</h2>
            <span className="card-text">
              <hr />
              {characters.length > 0 && <h3>CHARACTERS</h3>}
              <ul className="list-group list-group-flush">
                {characters.map((character, index) => (
                  <ListGroup key={index} message={character.name} />
                ))}
              </ul>
              {stories.length > 0 && <h3>STORIES</h3>}
              <ul className="list-group list-group-flush">
                {stories.map((story, index) => (
                  <ListGroup key={index} message={story.name} />
                ))}
              </ul>
            </span>
          </div>
        </div>
      </div>
    </div>
  );
};
