import React, { useCallback, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { getStories } from "../../store/actions/stories/storyAction";
import {
  resetPagination,
  setPagination,
} from "../../store/actions/pagination/paginationAction";
import { StoryCard } from "../../components/story-card/StoryCard";
import { Paginator } from "../../components/paginator/Paginator";
import { Loading } from "../../components/loading/Loading";

export const Stories = () => {
  const stories = useSelector((state) => state.stories.data);
  const isLoading = useSelector((state) => state.stories.isLoading);
  const paginator = useSelector((state) => state.paginator.paginator);
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(getStories());
  }, [dispatch]);

  useEffect(() => {
    if (stories.length) {
      dispatch(setPagination(1, stories));
      return () => dispatch(resetPagination());
    }
  }, [stories, dispatch]);

  const onPaginator = useCallback(
    (currentPage) => {
      dispatch(setPagination(currentPage, stories));
    },
    [stories, dispatch]
  );

  return (
    <article className="container">
      <h2 className="mt-5">STORIES</h2>

      {isLoading || stories.length === 0 ? (
        <Loading />
      ) : (
        paginator.paginatedData.map((story) => {
          return (
            <section key={story.id} className="row mt-3">
              <div className="col-12">
                <StoryCard
                  title={story.title}
                  description={story.description || "No description found"}
                  characters={story.characters ? story.characters.items : []}
                  comics={story.comics ? story.characters.items : []}
                />
              </div>
            </section>
          );
        })
      )}
      <section className="row">
        <article className="col-12">
          <Paginator
            onPaginate={onPaginator}
            paginatorData={paginator.paginatorData}
            pages={paginator.pages}
          />
        </article>
      </section>
    </article>
  );
};
