import React from "react";
import "./slider.scss";

const classNames = require("classnames");

export const Slider = ({ sliderId, children, indicators }) => {
  return (
    <div
      id={sliderId}
      className="carousel slide d-none d-md-block d-lg-block d-xl-block d-lg-none"
      data-ride="carousel"
    >
      <ol className="carousel-indicators">
        {indicators.map((indicator, index) => (
          <li
            key={index}
            data-target={`#${sliderId}`}
            data-slide-to={index}
            className={classNames(index === 0 ? "active" : "")}
          ></li>
        ))}
      </ol>
      <div className="carousel-inner">{children}</div>
      <a
        className="carousel-control-prev"
        href={`#${sliderId}`}
        role="button"
        data-slide="prev"
      >
        <span className="carousel-control-prev-icon" aria-hidden="true"></span>
        <span className="sr-only">Previous</span>
      </a>
      <a
        className="carousel-control-next"
        href={`#${sliderId}`}
        role="button"
        data-slide="next"
      >
        <span className="carousel-control-next-icon" aria-hidden="true"></span>
        <span className="sr-only">Next</span>
      </a>
    </div>
  );
};

Slider.Content = ({ title, message, image, active }) => {
  return (
    <div className={classNames("carousel-item", { active: active })}>
      <img src={image} className="w-100 img-fluid" alt="Slides" />
      <div className="carousel-caption d-none d-md-block text-container">
        <h5 className="text-slider">{title}</h5>
        <p className="text-slider">{message}</p>
      </div>
    </div>
  );
};

export default Slider;
