import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { ComicCard } from "../../components/comic-card/ComicCard";
import { getComicsByCharacter } from "../../store/actions/characters//characterAction";
import { Loading } from "../../components/loading/Loading";

export const ComicsByCharacter = ({ characterId }) => {
  const comics = useSelector((state) => state.characters.comicsByCharacter);
  const isLoading = useSelector((state) => state.characters.isLoading);
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(getComicsByCharacter(characterId));
  }, [characterId, dispatch]);

  return (
    <article className="container mt-3">
      <h2>COMICS BY CHARACTER</h2>
      <section className="row mt-3">
        {isLoading || comics.length === 0 ? (
          <div className="col-12 text-center">
            <Loading />
          </div>
        ) : (
          comics.length > 0 &&
          comics.map((comic) => (
            <article key={comic.id} className="col-md-6 col-sm-12">
              <ComicCard
                title={comic.title}
                image={`${comic.thumbnail.path}.${comic.thumbnail.extension}`}
                characters={comic.characters.items}
                stories={comic.stories.items}
              />
            </article>
          ))
        )}
      </section>
    </article>
  );
};
