import React, { useCallback, useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  resetPagination,
  setPagination,
} from "../../store/actions/pagination/paginationAction";
import { getCharacters } from "../../store/actions/characters/characterAction";
import { CharacterCard } from "../../components/character-card/CharacterCard";
import { Paginator } from "../../components/paginator/Paginator";
import { InputFilter } from "../../components/input-filter/InputFilter";
import { Loading } from "../../components/loading/Loading";
import {
  genericFilteredItem,
  genericFilterItemByObject,
} from "../../util/filterUtil";

export const Character = () => {
  const characters = useSelector((state) => state.characters.data);
  const isLoading = useSelector((state) => state.characters.isLoading);
  const paginator = useSelector((state) => state.paginator.paginator);
  const dispatch = useDispatch();
  const [currentPage, setCurrentPage] = useState(1);

  useEffect(() => {
    dispatch(getCharacters());
  }, [dispatch]);

  useEffect(() => {
    if (characters.length) {
      dispatch(setPagination(1, characters));
      return () => dispatch(resetPagination());
    }
  }, [characters, dispatch]);

  const onPaginator = useCallback(
    (currentPage) => {
      dispatch(setPagination(currentPage, characters));
      setCurrentPage(currentPage);
    },
    [characters, setCurrentPage, dispatch]
  );

  const onFilter = useCallback(
    (filterBy, filterText) => {
      if (filterText) {
        if (filterBy === "name") {
          const paginate = genericFilteredItem(
            filterText,
            filterBy,
            9,
            characters
          );
          dispatch(setPagination(1, paginate.filteredData));
        } else if (filterBy === "comics" || filterBy === "stories") {
          const paginate = genericFilterItemByObject(
            filterText,
            filterBy,
            9,
            characters
          );

          dispatch(setPagination(1, paginate.filteredData));
        }
      } else {
        onPaginator(currentPage);
      }
    },
    [characters, currentPage, dispatch, onPaginator]
  );

  return (
    <article className="container">
      <h2 className="mt-3">CHARACTERS</h2>
      <InputFilter
        onFilter={onFilter}
        filterByOptions={["name", "comics", "stories"]}
      />
      <section className="row">
        {isLoading || characters.length === 0 ? (
          <div className="col-12 text-center">
            <Loading />
          </div>
        ) : (
          paginator.paginatedData.map((character) => {
            return (
              <article key={character.id} className="col-md-4 col-sm-12 mt-3">
                <CharacterCard
                  characterId={character.id}
                  title={character.name}
                  description={character.description}
                  image={`${
                    character.thumbnail ? character.thumbnail.path : []
                  }.${
                    character.thumbnail ? character.thumbnail.extension : []
                  }`}
                />
              </article>
            );
          })
        )}
      </section>
      <section className="row">
        <article className="col-12">
          <Paginator
            onPaginate={onPaginator}
            paginatorData={paginator.paginatorData}
            pages={paginator.pages}
          />
        </article>
      </section>
    </article>
  );
};
