import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { getStoriesByCharacter } from "../../store/actions/characters/characterAction";
import { StoryCard } from "../../components/story-card/StoryCard";
import { Loading } from "../../components/loading/Loading";

export const StoryByCharacter = ({ characterId }) => {
  const stories = useSelector((state) => state.characters.storiesByCharacter);
  const isLoading = useSelector((state) => state.characters.isLoading);
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(getStoriesByCharacter(characterId));
  }, [characterId, dispatch]);

  return (
    <article className="container mt-3">
      <h2>STORIES BY CHARACTER</h2>

      {isLoading || stories.length === 0 ? (
        <div className="col-12 text-center">
          <Loading />
        </div>
      ) : (
        stories.map((story) => (
          <section key={story.id} className="row mt-3">
            <article className="col-12">
              <StoryCard
                key={story.id}
                title={story.title}
                description={story.description}
                characters={story.characters.items}
                comics={story.comics.items}
              />
            </article>
          </section>
        ))
      )}
    </article>
  );
};
