import React from "react";
import { A } from "hookrouter";
import { comicsByCharacter, storiesByCharacter } from "../../routes";

export const CharacterCard = ({ characterId, title, description, image }) => {
  return (
    <div className="card">
      <img src={image} className="card-img-top" alt="Characters" />
      <div className="card-body">
        <h2 className="card-title">{title}</h2>
        <p className="card-text">{description || "No description found!"}</p>
        <A
          href={`${comicsByCharacter}/${characterId}`}
          className="btn btn-outline-secondary text-dark mr-2"
        >
          Comics
        </A>
        <A
          href={`${storiesByCharacter}/${characterId}`}
          className="btn btn-outline-secondary text-dark ml-2"
        >
          Stories
        </A>
      </div>
    </div>
  );
};
